.wrapper {
  z-index: 999;
  position: absolute;
  top: 0;
  left: 50%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding: 16px;
  margin: 16px;

  width: 75%;

  border-radius: 16px;
  background-color: #f5f7f9;
  color: black;

  transform: translate(calc(-50% - 16px), calc(-100% - 16px));
  animation: out 7s;
}
@keyframes out {
  0% {
    transform: translate(calc(-50% - 16px), 0);
  }
  75% {
    transform: translate(calc(-50% - 16px), 0);
  }
  100% {
    transform: translate(calc(-50% - 16px), calc(-100% - 16px));
  }
}
